<template>
    <loading :active='isLoading' :is-full-page="fullPage" :loader='loader' />
    <div>
        <Alertbox :warning-text="warningText" @isAlert="warningText=''"></Alertbox>
        <Editor></Editor>
        <div class="logoutAndDel-container">
                <button id="logout-btn" @click="logOut">Log Out</button>
                <button id="del-btn" @click="delAcc">Delete Account</button>
        </div>
        <div class="home-btn" @click="goHome"><font-awesome-icon icon="home"/></div>
    </div>
</template>

<script>
    import Loading from 'vue-loading-overlay';      
    import 'vue-loading-overlay/dist/vue-loading.css';
    import Alertbox from '../components/alertbox'
    import Editor from '../components/editor'
    import AuthServices from '../../services/authServices.js'
    export default {
        components: {Alertbox, Editor, Loading },
        data() {
            return {
                warningText: '',
                isLoading: false,
                fullPage: true,
                loader: 'bars',
            }
        },
        methods: {
            async logOut(){
                let res = await AuthServices.logout();
                if(res.data.error){
                  return this.warningText = res.data.error;
                }
                this.$router.push('/')
            },
            async delAcc(){
                this.isLoading = true;
                let res = await AuthServices.delUserAcc();
                if(res.data.error){
                    this.isLoading = false;
                    console.log(res.data.error);
                    return this.warningText = res.data.error;
                }
                this.isLoading = false;
                this.$router.push('/')
            },
            goHome() {
                this.$router.push('/home')
            }
        },
        
    }
</script>

<style lang="scss" scoped>
    .backward-btn{
        cursor: pointer;
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        bottom: 2%;
        right: 2%;
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background-color: #fdfdfd;
        box-shadow: rgba(99, 99, 99, 0.404) 0px 2px 8px 0px;
        transition: all 0.1s ease;
        &:hover{
            transform: scale(1.05);
        }
    }
    .logoutAndDel-container{
        margin-top: 20%;
        margin-bottom: 1%;
        widows: 100%;
        text-align: center;
        #logout-btn,#del-btn{
            width: 12rem;
            height: 3rem;
            border-radius: 3px;
            outline: none;
            padding: 2px 5px;
            margin: 0 1rem;
            font-size: 1rem;
            cursor: pointer;
            &:hover{
                background-color: rgb(17, 16, 16);
                color: #fdfdfd;
            }
        }
    }
    .home-btn{
        cursor: pointer;
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        bottom: 2%;
        right: 2%;
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background-color: #fdfdfd;
        box-shadow: rgba(99, 99, 99, 0.404) 0px 2px 8px 0px;
        transition: all 0.1s ease;
        &:hover{
            transform: scale(1.05);
        }
    }
    @media screen and (max-width:768px){
        .logoutAndDel-container{
            margin-top: 77%;
            #logout-btn,#del-btn{
                width: 80%;
                margin: 0.5rem 0;
            }
        }
    }
</style>